main {
 height: 100%;
 width: 100%;
 background-color: #ffffff;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
padding: 20px;
border-radius: 10px;
box-shadow: 10px 10px 40px rgb(15, 9, 9);
 
}
section{
  height: 30vh;
  width: 40vw;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

}
input{
  padding: 5px 10px;
  border-radius: 10px;
  border: 2px solid grey;
  font-size: 18px;
}
button{
  height: 40px;
  width: 100px;
  padding: 5px;
  font-weight: bold;
  background-color: rgb(163, 71, 249);
  color: #ffffff;
  outline: none;
  border-radius: 18px;
  border: none;
  box-shadow: 1px 1px 8px black;
}
button:hover{
  background-color: rgb(109, 51, 164);
}
input:hover{
  border: 2px solid rgb(49, 49, 243);
}
.response_data{
  color: rgb(43, 50, 43);
}
